let that;
import uploadImg from '../../../../components/mc-image-upload/mc-image-upload.vue';
export default {
	data() {
		return {
			isok: true,
			serviceName: '基础火化服务',
			isUnits: '',
			isSelect: false,
			unitsList: ['天'],
			selectext: '',
			servicePrice: 400,
			kgList:0,
			//  冷冻 ， 清洁 ，寄存适用
			riskContent: [
				'提供恒温冷存。独立密封包装，保持宠物遗体的完整。方便宠主为爱宠另行选择吉时吉日送别。',
				'不用担心家里人忌讳反对，不用担心小孩或者其他宠物不小心打烂。独立寄存，方便省心，随时可以过来观看。',
				'整理遗容，清洁遗体，让宠物在最干净安详的状态下火化往生。'
			],
			serviceType: ''
		};
	},
	components: {
		uploadImg
	},
	onLoad(option) {
		that = this;
		//  打印上个页面传递的参数
		console.log(option.serviceType);
	},
	methods: {
		unitsClick() {
			that.isSelect = true;
		},
		selectUnits(e) {
			if (e) {
				this.selectext = e;
				setTimeout(() => {
					this.isSelect = false;
				}, 100);
			}
		}
		,addPriceBtn(){
			this.kgList++;
		}
	}
};
